import React, { Component } from 'react';
var { BlockPicker } = require('react-color');


const PIXEL_SIZE = 20

function Pixels({ pixels_list = [], style = {}, onClick = () => { } } = {}) {
  var list = pixels_list.map((pixel, index) => (
    <span style={{
      position: 'absolute',
      left: pixel.x * PIXEL_SIZE,
      top: pixel.y * PIXEL_SIZE,
      width: PIXEL_SIZE,
      height: PIXEL_SIZE,
      backgroundColor: pixel.color
    }} key={index}></span>
  ))
  return (
    <div style={style} onClick={onClick}>
      {list}
    </div>
  )
}

class App extends Component {
  constructor() {
    super();
    this.state = {
      pixels_list: [
      ],
      selectedPixel: null
    }
  }
  handlePixelClick({ clientX, clientY }) {
    var pixel = {
      x: Math.floor(clientX / PIXEL_SIZE),
      y: Math.floor(clientY / PIXEL_SIZE)
    }
    if ((pixel.x * PIXEL_SIZE + PIXEL_SIZE) >= window.innerWidth) {
      pixel.x --;
    } 
    if ((pixel.y * PIXEL_SIZE + PIXEL_SIZE) >= window.innerHeight) {
      pixel.y --;
    }
    this.setState({
      selectedPixel: pixel
    })
  }
  handleColorPicker({hex}){
    var pixel = {
      ...this.state.selectedPixel,
      color : hex
    }
    this.setState({
      pixels_list: this.state.pixels_list.filter(pixel_elem=>pixel_elem.x !== pixel.x || pixel_elem.y !== pixel.y).concat(pixel),
      selectedPixel: null
    })
  }
  render() {
    return (
      <div className="App" style={{
        position: 'absolute',
        width: '100%',
        height: '100%'
      }} >
        <Pixels pixels_list={this.state.pixels_list} onClick={this.handlePixelClick.bind(this)} style={{
          position: 'absolute',
          width: '100%',
          height: '100%',
          overflow: 'hidden'
        }} />
        {
          this.state.selectedPixel && <div style={{
            position: 'absolute',
            left: Math.min(Math.max(0, this.state.selectedPixel.x * PIXEL_SIZE - 75), window.innerWidth - 170),
            top: Math.min(Math.max(0, this.state.selectedPixel.y * PIXEL_SIZE + 35), window.innerHeight - 216),
          }}>
            <BlockPicker color="RED" onChangeComplete={this.handleColorPicker.bind(this)} />
          </div>
        }
      </div >
    );
  }
}

export default App;



